export default {
  props: {
    show: {
      type: Boolean,
      required: true,
    },
    columns: {
      type: Array,
      required: true,
    },
    value: {
      type: String,
    },
  },
  data() {
    return {};
  },
  watch: {
    // show(cur) {
    //   console.log(cur);
    //   if(cur) {
    //     this.$refs.picker && this.$refs.picker.setValues([this.value])
    //   }
    // }
  },
  computed: {
    regionList() {
      return this.columns ? this.columns.map((v) => v.name) : [];
    },
  },
  methods: {
    onConfirm(e) {
      let region = this.columns.find((v) => v.name === e);
      this.$emit("confirm", region);
    },
    onCancel(e) {
      this.$emit("update:show", false);
    },
  },
};
